import React, { Component } from "react";
// import { Container, Row, Col } from "reactstrap";
import backgroundImage from "../img/home-bg.png";
export default class HomePage extends Component {
  componentDidMount() {
    //   let elTop = ReactDOM.findDOMNode(this).getBoundingClientRect().top;
    //  this.props.updateScrollPosition(elTop)
  }
  render() {
    return (
      <section
        id={this.props.dataHistory}
        data-history={this.props.dataHistory}
        className={"swiper-slide single-slide homepage"}
      >
        <div className="content-wrapper">
          <div
            className="image-wrapper"
            style={{
              backgroundImage: `url(${backgroundImage})`,
              backgroundPosition: "center center",
              backgroundSize: "cover"
            }}
          >
            <div className="wrapper">
              <h2 className="heading">
                <div className="bar-1" />
                Witaj!
              </h2>
              {!this.props.isSmallScreen  && (
                <div className="left-animation">
                  <p className="paragraph">
                    Jeeli nadszed w Twoim yciu czas w ktrym zapragne
                    wybudowa swj wasny dom lub zmodernizowa dotychczasowy i
                    nie do koca wiesz w jaki sposb stworzy w nim odpowiedni
                    komfort termiczny dla Ciebie i Twojej rodziny zapoznaj si z
                    nasz ofert.
                  </p>
                  <p className="paragraph">
                    {" "}
                    <strong>
                      Jeli Ci zainteresuje zadzwo i umw si na spotkanie.
                      Wyjanimy wszystko dokadnie i bez zbdnych zawioci.
                    </strong>{" "}
                    Gwarantujemy fachowe doradztwo, rzetelne wsparcie techniczne
                    i sprawdzone rozwizania w dziedzinie utrzymania komfortu w
                    Twoim domu. Wykonamy projekt i wstpn kalkulacj kosztw za
                    darmo!
                  </p>
                </div>
              )}
            </div>
          </div>
          {this.props.isSmallScreen  && (
            <React.Fragment>
              <p className="paragraph">
                Jeeli nadszed w Twoim yciu czas w ktrym zapragne
                wybudowa swj wasny dom lub zmodernizowa dotychczasowy i nie
                do koca wiesz w jaki sposb stworzy w nim odpowiedni komfort
                termiczny dla Ciebie i Twojej rodziny zapoznaj si z nasz
                ofert.
              </p>
              <p className="paragraph">
                {" "}
                <strong>
                  Jeli Ci zainteresuje zadzwo i umw si na spotkanie.
                  Wyjanimy wszystko dokadnie i bez zbdnych zawioci.
                </strong>{" "}
                Gwarantujemy fachowe doradztwo, rzetelne wsparcie techniczne i
                sprawdzone rozwizania w dziedzinie utrzymania komfortu w Twoim
                domu. Wykonamy projekt i wstpn kalkulacj kosztw za darmo!
              </p>
            </React.Fragment>
          )}
        </div>
      </section>
    );
  }
}
