import React, { Component } from "react";
import ReactDOM from "react-dom";
import { Row, Col } from "reactstrap";
import slide1 from "../img/erv/slide1.png";
import slide2 from "../img/erv/slide2.png";
import slide3 from "../img/erv/slide3.png";
import Swiper from "react-id-swiper";

import { TweenLite, TimelineLite } from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";
const timing = 1;
export default class ERV extends Component {
  constructor(props) {
    super(props);
    this.ervSwiper = null;
  }

  componentDidMount() {
    this.ervSwiper = ReactDOM.findDOMNode(this).getElementsByClassName(
      "swiper-container"
    )[0].swiper;
    if (this.ervSwiper !== null) {
      this.props.setActiveSubpageIndex(this.ervSwiper, 0);
    }
  }

  render() {
    const params = {
      autoHeight: true,
      slideActiveClass: "swiper-slide-active erv-slide-active",
      loop: true,
      
      speed: timing * 1000,
      effect: "fade",
      fadeEffect: {
        crossFade: true
      },
      breakpointsInverse: true,
      breakpoints: {
        1200: {
          autoHeight: false
        }
      },
      pagination: {
        el: ".swiper-pagination-nested",
        clickable: true
      },
      on: {
        init: () => {
          this.ervSwiper = ReactDOM.findDOMNode(this).swiper;
        },
        transitionEnd: () => {
          TweenLite.to(window, 1, {
            scrollTo: {
              y: `#${this.props.dataHistorySubmenu.slug}`,
              offsetY: 110
            }
          });
        },
        slideNextTransitionStart: () => {
          this.activeSection = ReactDOM.findDOMNode(
            this
          ).getElementsByClassName("erv-slide-active")[0];
          this.header = this.activeSection.getElementsByClassName("heading")[0];
          this.bar1 = this.header.getElementsByClassName("bar-1")[0];

          this.leftAnimation = this.activeSection.getElementsByClassName(
            "left-animation"
          )[0];
          this.rightAnimation = this.activeSection.getElementsByClassName(
            "right-animation"
          )[0];

          if (
            this.leftAnimation !== undefined ||
            this.rightAnimation !== undefined
          ) {
            let tl = new TimelineLite();
            tl.to(this.bar1, timing, { width: "110%" })

              .fromTo(
                this.leftAnimation,
                timing,
                { y: 100, opacity: 0 },
                { y: 0, opacity: 1 },
                `-=${timing}`
              )

              .fromTo(
                this.rightAnimation,
                timing,
                { x: 100, opacity: 0 },
                { x: 0, opacity: 1 },
                `-=${timing}`
              )
              .to(this.bar1, timing / 2, { width: "0%" });
          }
        },
        slidePrevTransitionStart: () => {
          this.activeSection = ReactDOM.findDOMNode(
            this
          ).getElementsByClassName("erv-slide-active")[0];
          this.header = this.activeSection.getElementsByClassName("heading")[0];

          this.bar1 = this.header.getElementsByClassName("bar-1")[0];

          this.leftAnimation = this.activeSection.getElementsByClassName(
            "left-animation"
          )[0];
          this.rightAnimation = this.activeSection.getElementsByClassName(
            "right-animation"
          )[0];

          if (
            this.leftAnimation !== undefined ||
            this.rightAnimation !== undefined
          ) {
            let tl = new TimelineLite();
            tl.to(this.bar1, timing, { width: "110%" })

              // .fromTo(
              //   this.header,
              //   timing,
              //   { x: -100, opacity: 0 },
              //   { x: 0, opacity: 1 }
              // )
              .fromTo(
                this.leftAnimation,
                timing,
                { y: -100, opacity: 0 },
                { y: 0, opacity: 1 },
                `-=${timing}`
              );

            if (this.rightAnimation !== undefined) {
              tl.fromTo(
                this.rightAnimation,
                timing,
                { x: -100, opacity: 0 },
                { x: 0, opacity: 1 },
                `-=${timing}`
              );
            }
            tl.to(this.bar1, timing / 2, { width: "0%" });
          }
        }
      }
    };

    return (
      <div className="content-wrapper" id={this.props.dataHistorySubmenu.slug}>
        <Swiper {...params}>
          <Row noGutters className="slide-1">
            <Col xs={12} md={6}>
              <h2 className="heading"> <div className="bar-1" />Rekuperacja</h2>
              <div className="left-animation">
              <h3 className="subheading">Jakie ma zalety?</h3>
              <ul>
                <li>
                  <span>Zapewnia </span>
                  stay dopyw wieego powietrza niezalenie od pory roku
                </li>
                <li>
                  <span>W duej </span>
                  mierze zapobiega utracie ciepa zuytego na ogrzanie powietrza
                  w budynku
                </li>
                <li>
                  <span>Niweluje </span>
                  problem powstawania pleni i grzybw wewntrz pomieszcze,
                </li>
                <li>
                  <span>Doskonale </span>
                  sprawdza si w nowoczesnym (szczelnym) budownictwie, gdzie
                  wentylacja grawitacyjna nie spenia swojej funkcji
                </li>
                <li>
                  <span>Dziki </span>
                  zastosowaniu wentylacji mechanicznej z odzyskiem ciepa w celu
                  dostarczenia wieego powietrza nie musimy otwiera okien
                  dziki czemu unikamy przecigw jak i haasw dobiegajcych z
                  zewntrz
                </li>
                <li>
                  <span>Dostarcza </span>
                  powietrze od ktrego poprzez filtry zostay odseparowane pyy
                  i drobnoustroje
                </li>
                <li>
                  <span>Usuwa </span>z pomieszcze nieprzyjemne zapachy i
                  dwutlenek wgla
                </li>
              </ul>
            </div>
            </Col>
            <Col xs={12} md={6} className="right-animation">
              <div
                className="image-wrapper"
                style={{
                  backgroundImage: `url(${slide1})`,
                  backgroundPosition: "center center",
                  backgroundSize: "contain",
                  backgroundRepeat: "no-repeat"
                }}
              />
            </Col>
          </Row>
          <Row noGutters className="slide-2">
            <Col xs={12} md={6}>
              <h2 className="heading"> <div className="bar-1" />Rekuperacja</h2>

              <div className="left-animation">
              <h3 className="subheading">Co to takiego?</h3>
              <p className="paragraph">
                Rekuperacja to nic innego jak instalacja wentylacji mechanicznej
                z odzyskiem ciepa. Cao ukadu skada si z serca zwanego
                rekuperatorem oraz sieci kanaw doprowadzajcych wiee
                powietrze z zewntrz do tzw. Pomieszcze czystych (salon,
                sypialnia) oraz usuwajcych powietrze zuyte z pomieszcze tzw.
                brudnych (toaleta, kuchnia). Instalacja rekuperacji pozwala na
                prawidow wymian powietrza w budynku przez cay rok. Zjawisko
                odzysku ciepa pozwala na odzyskanie energii cieplnej z zuytego
                powietrza, a co za tym idzie umoliwia znaczne obnienie kosztw
                eksploatacyjnych.
              </p>
              </div>
            </Col>
            <Col xs={12} md={6} className="right-animation">
              <div
                className="image-wrapper"
                style={{
                  backgroundImage: `url(${slide2})`,
                  backgroundPosition: "center center",
                  backgroundSize: "contain",
                  backgroundRepeat: "no-repeat"
                }}
              />
            </Col>
          </Row>
          <Row noGutters className="slide-3">
            <Col xs={12} md={6}>
              <h2 className="heading"> <div className="bar-1" />Rekuperacja</h2>
              <div className="left-animation">
              <h3 className="subheading">Serce ukadu - rekuperator</h3>
              <p className="paragraph">
                Gwnym elementem urzdzenia jest wymiennik ktry odpowiedzialny
                jest za odzysk energii cieplnej. W celu wprowadzenia powietrza w
                ruch w obudow urzdzenia wbudowane s dwa wentylatory. Jeden
                odpowiedzialny jest za pobieranie wieego powietrza z zewntrz
                natomiast drugi za usuwanie zuytego. Filtry zainstalowane w
                urzdzeniu chroni pomieszczenia oraz gwny element urzdzenia
                przed zanieczyszczeniami.
              </p>
              </div>
            </Col>
            <Col xs={12} md={6} className="right-animation">
              <div
                className="image-wrapper"
                style={{
                  backgroundImage: `url(${slide3})`,
                  backgroundPosition: "center center",
                  backgroundSize: "contain",
                  backgroundRepeat: "no-repeat"
                }}
              />
            </Col>
          </Row>
        </Swiper>
        {this.props.isSmallScreen && (
          <button
            className="button"
            onClick={() => this.props.showSubpage(0)}
            style={{ margin: "0 auto" }}
          >
            <div className="button-chevron reversed" />
            Powrt
          </button>
        )}
      </div>
    );
  }
}
