import React, { Component } from "react";
import ReactDOM from "react-dom";
import { Row, Col } from "reactstrap";
import slide1 from "../img/ac/slide1.png";
import slide2 from "../img/ac/slide2.png";
import slide3 from "../img/ac/slide3.png";
import Swiper from "react-id-swiper";

import { TweenLite, TimelineLite } from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";
const timing = 1;
export default class AC extends Component {
  constructor(props) {
    super(props);
    this.acSwiper = null;
  }

  componentDidMount() {
    this.acSwiper = ReactDOM.findDOMNode(this).getElementsByClassName(
      "swiper-container"
    )[0].swiper;
    if( this.acSwiper!== null) {
      this.props.setActiveSubpageIndex(this.acSwiper, 1);
    }
   
  }

  render() {
    const params = {
      autoHeight: true,
      slideActiveClass: "swiper-slide-active ac-slide-active",
      loop: true,
     
      speed: timing * 1000,
      effect: "fade",
      fadeEffect: {
        crossFade: true
      },
      breakpointsInverse: true,
      breakpoints: {
        1200: {
          autoHeight: false
        }
      },
      pagination: {
        el: ".swiper-pagination-nested",
        clickable: true
      },
      on: {
        init: () => {
          this.acSwiper = ReactDOM.findDOMNode(this).swiper;
        },
        transitionEnd: () => {
          TweenLite.to(window, 1, {
            scrollTo: {
              y: `#${this.props.dataHistorySubmenu.slug}`,
              offsetY: 110
            }
          });
        },
        slideNextTransitionStart: () => {
          this.activeSection = ReactDOM.findDOMNode(
            this
          ).getElementsByClassName("ac-slide-active")[0];
          this.header = this.activeSection.getElementsByClassName("heading")[0];
          this.bar1 = this.header.getElementsByClassName("bar-1")[0];

          this.leftAnimation = this.activeSection.getElementsByClassName(
            "left-animation"
          )[0];
          this.rightAnimation = this.activeSection.getElementsByClassName(
            "right-animation"
          )[0];

          if (
            this.leftAnimation !== undefined ||
            this.rightAnimation !== undefined
          ) {
            let tl = new TimelineLite();
            tl.to(this.bar1, timing, { width: "110%" })

              .fromTo(
                this.leftAnimation,
                timing,
                { y: 100, opacity: 0 },
                { y: 0, opacity: 1 },
                `-=${timing}`
              )

              .fromTo(
                this.rightAnimation,
                timing,
                { x: 100, opacity: 0 },
                { x: 0, opacity: 1 },
                `-=${timing}`
              )
              .to(this.bar1, timing / 2, { width: "0%" });
          }
        },
        slidePrevTransitionStart: () => {
          this.activeSection = ReactDOM.findDOMNode(
            this
          ).getElementsByClassName("ac-slide-active")[0];
          this.header = this.activeSection.getElementsByClassName("heading")[0];

          this.bar1 = this.header.getElementsByClassName("bar-1")[0];

          this.leftAnimation = this.activeSection.getElementsByClassName(
            "left-animation"
          )[0];
          this.rightAnimation = this.activeSection.getElementsByClassName(
            "right-animation"
          )[0];

          if (
            this.leftAnimation !== undefined ||
            this.rightAnimation !== undefined
          ) {
            let tl = new TimelineLite();
            tl.to(this.bar1, timing, { width: "110%" })

              // .fromTo(
              //   this.header,
              //   timing,
              //   { x: -100, opacity: 0 },
              //   { x: 0, opacity: 1 }
              // )
              .fromTo(
                this.leftAnimation,
                timing,
                { y: -100, opacity: 0 },
                { y: 0, opacity: 1 },
                `-=${timing}`
              );

            if (this.rightAnimation !== undefined) {
              tl.fromTo(
                this.rightAnimation,
                timing,
                { x: -100, opacity: 0 },
                { x: 0, opacity: 1 },
                `-=${timing}`
              );
            }
            tl.to(this.bar1, timing / 2, { width: "0%" });
          }
        }
      }
    };

    return (
      <div className="content-wrapper"  id={this.props.dataHistorySubmenu.slug}>
        

        <Swiper {...params}>
          <Row noGutters className="slide-1">
            <Col xs={12} md={6}>
            <h2 className="heading"><div className="bar-1" /> Klimatyzacja</h2>
            <div className="left-animation">
              <h3 className="subheading">Po co montowa klimatyzacj?</h3>
              <p className="paragraph">
                Coraz wiksz uwag przykadamy do komfortu w jakim pracujemy
                bd spdzamy czas w domu. Dwoma najwaniejszymi czynnikami
                wpywajcymi na komfort cieplny czowieka jest zachowana na
                odpowiednim poziomie temperatura oraz wilgotno otoczenia. Za
                pomoc urzdze klimatyzacyjnych moemy kontrolowa te zmienne
                na tyle aby ich warto byy dokadnie dopasowane do naszego
                organizmu.
              </p>
            </div>
            </Col>
            <Col xs={12} md={6} className="right-animation">
              <div
                className="image-wrapper"
                style={{
                  backgroundImage: `url(${slide1})`,
                  backgroundPosition: "center center",
                 backgroundSize: "contain",
                  backgroundRepeat: "no-repeat"
                }}
              />
            </Col>
          </Row>
          <Row noGutters className="slide-2">
            <Col xs={12} md={6}>
            <h2 className="heading"><div className="bar-1" /> Klimatyzacja</h2>
            <div className="left-animation">
              <h3 className="subheading">Jak dziaa klimatyzacja?</h3>
              <p className="paragraph">
                Czynnik chodniczy jako chodna ciecz dostaje si do wymiennika
                ciepa wewntrz pomieszczenia (parownik). Pod wpywem dziaania
                wentylatora ciepe powietrze pobierane jest z pomieszczenia. W
                tym samym czasie przechodzi ono przez parownik w ktrym zachodzi
                zjawisko odparowania polegajce na odebraniu energii cieplnej
                przez czynnik bdcy w tym czasie w wymienniku czego produktem
                ubocznym jest schodzone powietrze.
              </p>
            </div>
            </Col>
            <Col xs={12} md={6} className="right-animation">
              <div
                className="image-wrapper"
                style={{
                  backgroundImage: `url(${slide2})`,
                  backgroundPosition: "center center",
                 backgroundSize: "contain",
                  backgroundRepeat: "no-repeat"
                }}
              />
            </Col>
          </Row>
          <Row noGutters className="slide-3">
            <Col xs={12} md={6}>
            <h2 className="heading"><div className="bar-1" /> Klimatyzacja</h2>
            <div className="left-animation">
              <h3 className="subheading">
                Najczciej stosowane rozwizania w domach
              </h3>
              <p className="paragraph">
                Najczciej stosowane rozwizania w domkach jednorodzinnych lub
                mieszkaniach okrelane s jako ukady SPLIT i MULTISPLIT.
              </p>
              <ul>
                <li>
                  <span className="font-weight-800 text-red">SPLIT</span> 
                  ukad w ktrym jedna jednostka zewntrzna poczona jest z
                  jedn jednostk wewntrzn.
                </li>

                <li>
                  <span className="font-weight-800 text-red">MULTISPLIT</span> 
                  ukad w ktrym jedna jednostka zewntrzna moe obsugiwa od 2
                  do 8 jednostek wewntrznych. Doskonale sprawdza si w sytuacji
                  w ktrej chcemy zapewni klimatyzacj dla kadego
                  pomieszczenia w budynku.
                </li>
              </ul>
              <p className="paragraph">
                Najczciej spotykanymi modelami s jednostki cienne oraz
                jednostki kasetonowe.
              </p>
           </div>
            </Col>
            <Col xs={12} md={6} className="right-animation">
              <div
                className="image-wrapper"
                style={{
                  backgroundImage: `url(${slide3})`,
                  backgroundPosition: "center center",
                 backgroundSize: "contain",
                  backgroundRepeat: "no-repeat"
                }}
              />
            </Col>
          </Row>
        </Swiper>
        {this.props.isSmallScreen  && (
        <button
          className="button"
          onClick={() => this.props.showSubpage(1)}
          style={{ margin: "0 auto" }}
        >
          <div className="button-chevron reversed" />
          Powrt
        </button>
        )}
      </div>
    );
  }
}
